.dots {
  display: -webkit-flex;
  display: flex;
  overflow: hidden;
  position: relative;
}
.dots-left::after {
    color: gray;
    content: ". . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . . ";
    font-weight: normal;
    display: inline-block;
    position: absolute;
    white-space: nowrap; /* only needed when content has spaces */
    margin-left: 5px; /* space left of dots */
}

.dot-leader__left {
    position: relative;
    display: inline-block;
}

.dots-right {
    background: white; /* cover the dots */
    display: inline-block;
    position: absolute;
    right: 0;
    padding-left: 5px; /* space right of dots */
    padding-right: 10px;
}

.img-scroll {
    overflow: auto;
    white-space: nowrap;
}

div.img-scroll img {
    display: inline-block;
    height:335px;
    width: 335px;
    margin: 0 5px 0 5px;
